<template>
  <div class="wrapper pa-4">
    <h1>0宽字符隐写</h1>
    <div class="d-flex align-center my-2">
      <div>明文length: {{ text.length }}</div>
      <VSpacer />
      <v-btn 
        class="ml-2"
        @click="text = ''"
      >
        清空明文
      </v-btn>
      <v-btn 
        class="ml-2"
        color="error"
        @click="reset"
      >
        清空全部
      </v-btn>
    </div>
    <textarea
      ref="textarea"
      v-model="text"
      class="pa-2"
      style="width: 100%;border: 1px solid lightblue;"
      rows="6"
    />
    <div class="d-flex align-center my-2">
      <div>加密内容length: {{ injecttext.length }}</div>
      <VSpacer />
      <v-btn 
        class="ml-2"
        @click="injecttext = ''"
      >
        清空秘文
      </v-btn>
      <v-btn 
        class="ml-2"
        color="primary"
        @click="encode"
      >
        加密明文
      </v-btn>
    </div>
    <textarea
      ref="textarea"
      v-model="injecttext"
      class="pa-2"
      style="width: 100%;border: 1px solid lightblue;"
      rows="6"
    />
    <div class="d-flex align-center my-2">
      <div>结果length: {{ result.length }}</div>
      <VSpacer />
      <v-btn 
        class="ml-2"
        @click="result = ''"
      >
        清空结果
      </v-btn>
      <v-btn
        class="ml-2"
        color="primary"
        @click="decode"
      >
        解密结果
      </v-btn>
    </div>
    <textarea
      ref="textarea"
      v-model="result"
      class="pa-2"
      style="width: 100%;border: 1px solid lightblue;"
      rows="6"
    />

    <p />
    <p>参考<a href="https://330k.github.io/misc_tools/unicode_steganography.html">https://330k.github.io/misc_tools/unicode_steganography.html</a></p>
  </div>
</template>

<script>
import { unicodeSteganographer } from './unicode_steganography.js';

export default {
  data() {
    return {
      text: 'haha',
      injecttext: 'wode',
      result: '',
    }
  },

  methods: {
    async reset() {
      if (await this.$root.$confirm.open(
        '确认',
        '确定清空吗？',
      )) {
        this.text = '';
        this.injecttext = '';
        this.result = '';
      }
    },
    encode() {
      this.result = unicodeSteganographer.encodeText(this.text, this.injecttext);
      console.log('encodeed result:', this.result, typeof this.result);
    },
    decode() {
      const decodeedObj = unicodeSteganographer.decodeText(this.result);
      console.log('decodeed result:', decodeedObj);
      this.text = decodeedObj.originalText;
      this.injecttext = decodeedObj.hiddenText;
    },
  },
}
</script>

<style scoped>
.wrapper {
  display: grid;
  grid-template-columns:
    1fr
    min(65ch, 100%)
    1fr;
}

.wrapper > * {
  grid-column: 2;
}

.full-bleed {
  width: 100%;
  grid-column: 1 / -1;
}

</style>